import React from "react";
import ReactSpinningWheel from "react-spinning-wheel";
import "react-spinning-wheel/dist/style.css";

const Spinner = ({ onGameFinish }) => {
  const segments = [
    { option: "Prize 1", id: "prize1" },
    { option: "Prize 2", id: "prize2" },
    { option: "Prize 3", id: "prize3" },
    { option: "Prize 4", id: "prize4" },
    { option: "Prize 5", id: "prize5" },
  ];

  const [spinning, setSpinning] = React.useState(false);

  const handleSpinClick = () => {
    setSpinning(true);
    setTimeout(() => {
      setSpinning(false);
      const wonAmount = Math.random() * 100; // Random prize amount
      onGameFinish(wonAmount); // Callback to handle the game finish
    }, 3000); // Simulate spinning time
  };

  return (
    <div>
      <ReactSpinningWheel
        segments={segments}
        spin={spinning}
        onComplete={() => console.log("Spin complete!")}
      />
      <button
        onClick={handleSpinClick}
        className="mt-6 p-2 bg-blue-500 text-white rounded-lg"
      >
        Spin
      </button>
    </div>
  );
};

export default Spinner;
